nav.navbar,
nav.navbar .navbar-menu {
    background-color: transparent;
    padding-top: 10px;
}
nav.navbar.cor-topo .navbar-end.menu-superior {
    background: rgba(150, 150, 150, 0.40);
}
nav.navbar .navbar-end.menu-superior {
    border-radius: 5px;
}
nav.navbar .navbar-end.menu-superior a.navbar-item {
    color: #E3E3E3;
    opacity: 0.7;
    transition: 0.6s;
}
nav.navbar .navbar-end.menu-superior a.navbar-item:hover {
    color: #E3E3E3;
    background-color: rgba(150, 150, 150, 0.66);
    border-radius: 5px;
    opacity: 1;
}
nav.navbar .navbar-burger {
    color: #FFF;
}
nav.navbar.cor-scroll {
    background-color:#CACACA;
}
nav.navbar.cor-scroll .navbar-menu,
nav.navbar.cor-scroll {
    padding-top: 0;
}
nav.navbar.cor-scroll a {

    color: #000 !important;
}
nav.navbar .navbar-menu {
    padding-right: 30px;
}
nav.navbar {
    transition: 1s;
}
nav.navbar.cor-scroll,
nav.navbar.cor-topo {
    transition-duration: 0.5s;
    transition-timing-function: ease;
}

* {
	scroll-behavior: smooth;
}
@media screen and (max-width: 1023px) {
    .navbar.is-fixed-top .navbar-menu .navbar-end.menu-superior {
        background-color: #CACACA;
    }
    .navbar.is-fixed-top .navbar-menu {
        padding: 0;
    }
    .navbar.is-fixed-top .navbar-menu .navbar-end.menu-superior a.navbar-item {
        color: #000;
    }
}
